button.close {
  border: none;
  background-color: transparent;
}
.error-msg {
  color: rgb(206, 2, 2);
}
.form-control.invalid-input {
  border: 1px solid rgb(206, 2, 2);
  -webkit-box-shadow: 0px 0px 10px 3px rgba(206, 2, 2, 0.63); 
  box-shadow: 0px 0px 10px 3px rgba(206, 2, 2, 0.63);
}
